@import url("https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@300;500;700&family=Roboto:wght@300;400;500;700&display=swap");

body {
  margin: 0;
  font-family: "Montserrat Alternates", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
